.subscribe_section {
  position: relative; }

.subscribe__block {
  background: linear-gradient(126.53deg, #ECF2FF 32.18%, #A2EEFF 109.84%);
  border-radius: 8px;
  padding: 35px 80px;
  display: flex;
  align-items: center;
  justify-content: space-between; }

.subscribe_text {
  width: 45%; }

.subscribe_item {
  width: 45%;
  padding: 5px;
  background: rgba(255, 255, 255, 0.5);
  border-radius: 8px;
  display: flex;
  align-items: center; }

.subscribe_input {
  background: transparent;
  outline: none;
  border: none;
  width: 100%;
  height: 100%;
  padding: 15px; }

@media (max-width: 992px) {
  .subscribe__block {
    flex-direction: column; }
  .subscribe_text {
    width: 100%; }
  .subscribe_item {
    background: transparent;
    flex-direction: column;
    width: 100%;
    padding: 0; }
  .subscribe_input {
    background: rgba(255, 255, 255, 0.5);
    border-radius: 8px;
    margin-top: 15px;
    margin-bottom: 10px; } }

@media (max-width: 500px) {
  .subscribe_btn {
    width: 100%; }
  .subscribe__block {
    padding: 20px 10px; }
  .subscribe_text {
    padding: 0px 15px; } }
