.comment_star {
  margin-right: -40px; }

.catalog_title {
  display: block;
  width: 100%;
  text-align: center;
  margin-top: 20px;
  margin-bottom: 20px; }

.card__block {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin: 0px -16px; }

.card_rating .form_stars {
  transform: scale(0.5); }
  .card_rating .form_stars.blocked {
    pointer-events: none; }

.card_hint {
  position: relative;
  margin-bottom: 5px;
  text-align: center;
  width: fit-content;
  width: -moz-fit-content; }
  .card_hint__block {
    display: flex;
    flex-direction: column;
    position: absolute;
    z-index: 9;
    top: 20px;
    right: 0;
    justify-content: flex-end;
    align-items: flex-end; }
  .card_hint.new {
    background: #000;
    padding: 4px 12px; }
  .card_hint.sale {
    background: #EB5757;
    padding: 4px 16px; }

.card_item {
  transition: all .2s;
  position: relative;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
  background: #FAFAFA;
  border-radius: 8px;
  margin: 16px;
  width: calc(25% - 32px);
  height: 535px; }
  .card_item:hover {
    padding-bottom: 120px; }
    .card_item:hover .card_hidden__block {
      bottom: 10px; }
    .card_item:hover .card_img {
      width: 90%;
      top: -20px; }

.card_swiper {
  position: relative;
  padding-bottom: 60px; }
  .card_swiper .card_item {
    margin: 0; }
  .card_swiper .swiper-pagination {
    bottom: 20px !important;
    left: 0px !important;
    display: flex;
    justify-content: center; }

.card_info {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: space-between;
  margin: 10px 0; }

.card_title {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  text-align: center;
  height: 65px;
  margin-bottom: 8px; }

.card_price__block {
  display: flex;
  align-items: center; }

.card_old_price {
  margin-right: 5px;
  text-decoration: line-through; }

.card_img {
  position: relative;
  top: 0;
  transition: all .2s;
  width: 100%;
  height: 350px;
  overflow: hidden; }
  .card_img img {
    transition: all .2s;
    width: 100%;
    height: 100%;
    object-fit: contain; }

.card_hidden__block {
  transition: all .2s;
  position: absolute;
  bottom: -92px;
  display: flex;
  flex-direction: column;
  width: 100%;
  margin-top: 15px;
  align-items: center;
  justify-content: center; }

.card_fast_buy {
  transition: all .2s;
  margin-top: 12px;
  cursor: pointer;
  padding-bottom: 3px;
  border-bottom: 1px solid transparent; }
  .card_fast_buy:hover {
    border-bottom: 1px solid #73CAC2; }

.card_like {
  transition: all .2s;
  padding: 12px;
  width: 50px;
  height: 50px;
  margin-left: 20px;
  background: #FFFFFF;
  border-radius: 8px;
  display: flex;
  cursor: pointer;
  flex-direction: column;
  justify-content: center;
  align-items: center; }
  .card_like .card_heart {
    transition: all .2s; }
    .card_like .card_heart_2 {
      transition: all .2s; }
  .card_like:hover {
    background: #73CAC2; }
    .card_like:hover .card_heart {
      stroke: #fff; }
      .card_like:hover .card_heart_2 {
        stroke: #EB5757; }

.card_btn {
  box-shadow: 0px 10px 10px rgba(115, 202, 194, 0.5); }
  .card_btn__block {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%; }

@media (max-width: 1300px) {
  .card_item {
    width: calc(50% - 32px); } }

@media (max-width: 660px) {
  .card_item {
    width: 100%;
    margin: 20px 0; }
  .card__block {
    margin: 0; } }
